.HpChange_textbox{
    text-align: center;
    display: inline-flex;
    width: 20rem;
    height: 2.9rem;
    padding: 0.5rem 4rem 0.5rem 4rem;
    align-items: center;
    gap: 2.65631rem;

    border-radius: 0.125rem;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background-color: RGB(238, 250, 234);
}

.CCgroup-child {
    position: absolute;
    width: 100%;
    top: 0;
    right: 0;
    left: 0;
    border-radius: 3px;
    background-color: #48685e;
    height: 2.19rem;
  }
  .CCdiv {
    position: absolute;
    width: calc(100% - 174px);
    top: 0.44rem;
    left: 5.38rem;
    letter-spacing: -0.01em;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 1.25rem;
  }
  .CCbirdie-buddy,
  .CCrectangle-parent {
    position: absolute;
    text-align: center;
  }
  .CCrectangle-parent {
    width: calc(100% - 30px);
